	module.exports = {"data":{"postsJson":{"title":"Faith in Action","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"James","ref":"1:1-5:20","html":"<p>   <strong>1</strong><u>1</u>James, a servant of God and of the Lord Jesus Christ, to the twelve tribes which are in the Dispersion: Greetings. <u>2</u>Count it all joy, my brothers,* when you fall into various temptations, <u>3</u>knowing that the testing of your faith produces endurance. <u>4</u>Let endurance have its perfect work, that you may be perfect and complete, lacking in nothing. <u>5</u>But if any of you lacks wisdom, let him ask of God, who gives to all liberally and without reproach; and it will be given to him. <u>6</u>But let him ask in faith, without any doubting, for he who doubts is like a wave of the sea, driven by the wind and tossed. <u>7</u>For let that man not think that he will receive anything from the Lord. <u>8</u>He is a double-minded man, unstable in all his ways.<br />   <u>9</u>But let the brother in humble circumstances glory in his high position; <u>10</u>and the rich, in that he is made humble, because like the flower in the grass, he will pass away. <u>11</u>For the sun arises with the scorching wind, and withers the grass, and the flower in it falls, and the beauty of its appearance perishes. So also will the rich man fade away in his pursuits.<br />   <u>12</u>Blessed is the man who endures temptation, for when he has been approved, he will receive the crown of life, which the Lord promised to those who love him.<br />   <u>13</u>Let no man say when he is tempted, “I am tempted by God,” for God can’t be tempted by evil, and he himself tempts no one. <u>14</u>But each one is tempted when he is drawn away by his own lust, and enticed. <u>15</u>Then the lust, when it has conceived, bears sin; and the sin, when it is full grown, produces death. <u>16</u>Don’t be deceived, my beloved brothers. <u>17</u>Every good gift and every perfect gift is from above, coming down from the Father of lights, with whom can be no variation, nor turning shadow. <u>18</u>Of his own will he gave birth to us by the word of truth, that we should be a kind of first fruits of his creatures.<br />   <u>19</u>So, then, my beloved brothers, let every man be swift to hear, slow to speak, and slow to anger; <u>20</u>for the anger of man doesn’t produce the righteousness of God. <u>21</u>Therefore, putting away all filthiness and overflowing of wickedness, receive with humility the implanted word, which is able to save your souls.* <u>22</u>But be doers of the word, and not only hearers, deluding your own selves. <u>23</u>For if anyone is a hearer of the word and not a doer, he is like a man looking at his natural face in a mirror; <u>24</u>for he sees himself, and goes away, and immediately forgets what kind of man he was. <u>25</u>But he who looks into the perfect law of freedom, and continues, not being a hearer who forgets, but a doer of the work, this man will be blessed in what he does.<br />   <u>26</u>If anyone among you thinks himself to be religious while he doesn’t bridle his tongue, but deceives his heart, this man’s religion is worthless. <u>27</u>Pure religion and undefiled before our God and Father is this: to visit the fatherless and widows in their affliction, and to keep oneself unstained by the world.<br />   <strong>2</strong><u>1</u>My brothers, don’t hold the faith of our Lord Jesus Christ of glory with partiality. <u>2</u>For if a man with a gold ring, in fine clothing, comes into your synagogue,* and a poor man in filthy clothing also comes in; <u>3</u>and you pay special attention to him who wears the fine clothing, and say, “Sit here in a good place”; and you tell the poor man, “Stand there,” or “Sit by my footstool”; <u>4</u>haven’t you shown partiality among yourselves, and become judges with evil thoughts? <u>5</u>Listen, my beloved brothers. Didn’t God choose those who are poor in this world to be rich in faith, and heirs of the Kingdom which he promised to those who love him? <u>6</u>But you have dishonored the poor man. Don’t the rich oppress you, and personally drag you before the courts? <u>7</u>Don’t they blaspheme the honorable name by which you are called? <u>8</u>However, if you fulfill the royal law, according to the Scripture, “You shall love your neighbor as yourself,”* you do well. <u>9</u>But if you show partiality, you commit sin, being convicted by the law as transgressors. <u>10</u>For whoever keeps the whole law, and yet stumbles in one point, he has become guilty of all. <u>11</u>For he who said, “Do not commit adultery,”* also said, “Do not commit murder.”* Now if you do not commit adultery, but murder, you have become a transgressor of the law. <u>12</u>So speak, and so do, as men who are to be judged by a law of freedom. <u>13</u>For judgment is without mercy to him who has shown no mercy. Mercy triumphs over judgment.<br />   <u>14</u>What good is it, my brothers, if a man says he has faith, but has no works? Can faith save him? <u>15</u>And if a brother or sister is naked and in lack of daily food, <u>16</u>and one of you tells them, “Go in peace, be warmed and filled”; and yet you didn’t give them the things the body needs, what good is it? <u>17</u>Even so faith, if it has no works, is dead in itself. <u>18</u>Yes, a man will say, “You have faith, and I have works.” Show me your faith without works, and I by my works will show you my faith.<br />   <u>19</u>You believe that God is one. You do well. The demons also believe, and shudder. <u>20</u>But do you want to know, vain man, that faith apart from works is dead? <u>21</u>Wasn’t Abraham our father justified by works, in that he offered up Isaac his son on the altar? <u>22</u>You see that faith worked with his works, and by works faith was perfected; <u>23</u>and the Scripture was fulfilled which says, “Abraham believed God, and it was accounted to him as righteousness”;* and he was called the friend of God. <u>24</u>You see then that by works, a man is justified, and not only by faith. <u>25</u>In the same way, wasn’t Rahab the prostitute also justified by works, in that she received the messengers, and sent them out another way? <u>26</u>For as the body apart from the spirit is dead, even so faith apart from works is dead.<br />   <strong>3</strong><u>1</u>Let not many of you be teachers, my brothers, knowing that we will receive heavier judgment. <u>2</u>For in many things we all stumble. If anyone doesn’t stumble in word, the same is a perfect man, able to bridle the whole body also. <u>3</u>Indeed, we put bits into the horses’ mouths so that they may obey us, and we guide their whole body. <u>4</u>Behold,* the ships also, though they are so big and are driven by fierce winds, are yet guided by a very small rudder, wherever the pilot desires. <u>5</u>So the tongue is also a little member, and boasts great things. See how a small fire can spread to a large forest! <u>6</u>And the tongue is a fire. The world of iniquity among our members is the tongue, which defiles the whole body, and sets on fire the course of nature, and is set on fire by Gehenna.* <u>7</u>For every kind of animal, bird, creeping thing, and sea creature, is tamed, and has been tamed by mankind. <u>8</u>But nobody can tame the tongue. It is a restless evil, full of deadly poison. <u>9</u>With it we bless our God and Father, and with it we curse men, who are made in the image of God. <u>10</u>Out of the same mouth comes blessing and cursing. My brothers, these things ought not to be so. <u>11</u>Does a spring send out from the same opening fresh and bitter water? <u>12</u>Can a fig tree, my brothers, yield olives, or a vine figs? Thus no spring yields both salt water and fresh water.<br />   <u>13</u>Who is wise and understanding among you? Let him show by his good conduct that his deeds are done in gentleness of wisdom. <u>14</u>But if you have bitter jealousy and selfish ambition in your heart, don’t boast and don’t lie against the truth. <u>15</u>This wisdom is not that which comes down from above, but is earthly, sensual, and demonic. <u>16</u>For where jealousy and selfish ambition are, there is confusion and every evil deed. <u>17</u>But the wisdom that is from above is first pure, then peaceful, gentle, reasonable, full of mercy and good fruits, without partiality, and without hypocrisy. <u>18</u>Now the fruit of righteousness is sown in peace by those who make peace.<br />   <strong>4</strong><u>1</u>Where do wars and fightings among you come from? Don’t they come from your pleasures that war in your members? <u>2</u>You lust, and don’t have. You murder and covet, and can’t obtain. You fight and make war. You don’t have, because you don’t ask. <u>3</u>You ask, and don’t receive, because you ask with wrong motives, so that you may spend it for your pleasures. <u>4</u>You adulterers and adulteresses, don’t you know that friendship with the world is hostility toward God? Whoever therefore wants to be a friend of the world makes himself an enemy of God. <u>5</u>Or do you think that the Scripture says in vain, “The Spirit who lives in us yearns jealously”? <u>6</u>But he gives more grace. Therefore it says, “God resists the proud, but gives grace to the humble.”* <u>7</u>Be subject therefore to God. But resist the devil, and he will flee from you. <u>8</u>Draw near to God, and he will draw near to you. Cleanse your hands, you sinners; and purify your hearts, you double-minded. <u>9</u>Lament, mourn, and weep. Let your laughter be turned to mourning, and your joy to gloom. <u>10</u>Humble yourselves in the sight of the Lord, and he will exalt you.<br />   <u>11</u>Don’t speak against one another, brothers. He who speaks against a brother and judges his brother, speaks against the law and judges the law. But if you judge the law, you are not a doer of the law, but a judge. <u>12</u>Only one is the lawgiver, who is able to save and to destroy. But who are you to judge another?<br />   <u>13</u>Come now, you who say, “Today or tomorrow let’s go into this city, and spend a year there, trade, and make a profit.” <u>14</u>Whereas you don’t know what your life will be like tomorrow. For what is your life? For you are a vapor, that appears for a little time, and then vanishes away. <u>15</u>For you ought to say, “If the Lord wills, we will both live, and do this or that.” <u>16</u>But now you glory in your boasting. All such boasting is evil. <u>17</u>To him therefore who knows to do good, and doesn’t do it, to him it is sin.<br />   <strong>5</strong><u>1</u>Come now, you rich, weep and howl for your miseries that are coming on you. <u>2</u>Your riches are corrupted and your garments are moth-eaten. <u>3</u>Your gold and your silver are corroded, and their corrosion will be for a testimony against you, and will eat your flesh like fire. You have laid up your treasure in the last days. <u>4</u>Behold, the wages of the laborers who mowed your fields, which you have kept back by fraud, cry out, and the cries of those who reaped have entered into the ears of the Lord of Armies.* <u>5</u>You have lived delicately on the earth, and taken your pleasure. You have nourished your hearts as in a day of slaughter. <u>6</u>You have condemned, you have murdered the righteous one. He doesn’t resist you.<br />   <u>7</u>Be patient therefore, brothers, until the coming of the Lord. Behold, the farmer waits for the precious fruit of the earth, being patient over it, until it receives the early and late rain. <u>8</u>You also be patient. Establish your hearts, for the coming of the Lord is at hand.<br />   <u>9</u>Don’t grumble, brothers, against one another, so that you won’t be judged. Behold, the judge stands at the door. <u>10</u>Take, brothers, for an example of suffering and of patience, the prophets who spoke in the name of the Lord. <u>11</u>Behold, we call them blessed who endured. You have heard of the patience of Job, and have seen the Lord in the outcome, and how the Lord is full of compassion and mercy. <u>12</u>But above all things, my brothers, don’t swear— not by heaven, or by the earth, or by any other oath; but let your “yes” be “yes”, and your “no”, “no”; so that you don’t fall into hypocrisy.*<br />   <u>13</u>Is any among you suffering? Let him pray. Is any cheerful? Let him sing praises. <u>14</u>Is any among you sick? Let him call for the elders of the assembly, and let them pray over him, anointing him with oil in the name of the Lord, <u>15</u>and the prayer of faith will heal him who is sick, and the Lord will raise him up. If he has committed sins, he will be forgiven. <u>16</u>Confess your offenses to one another, and pray for one another, that you may be healed. The insistent prayer of a righteous person is powerfully effective. <u>17</u>Elijah was a man with a nature like ours, and he prayed earnestly that it might not rain, and it didn’t rain on the earth for three years and six months. <u>18</u>He prayed again, and the sky gave rain, and the earth produced its fruit.<br />   <u>19</u>Brothers, if any among you wanders from the truth and someone turns him back, <u>20</u>let him know that he who turns a sinner from the error of his way will save a soul from death and will cover a multitude of sins.</p><h5>*James 1:2 The word for “brothers” here and where context allows may also be correctly translated “brothers and sisters” or “siblings.”<br />*James 1:21 or, preserve your life.<br />*James 2:2 or, meeting<br />*James 2:8 Leviticus 19:18<br />*James 2:11 Exodus 20:14; Deuteronomy 5:18<br />*James 2:11 Exodus 10:13; Deuteronomy 5:17<br />*James 2:23 Genesis 15:6<br />*James 3:4 “Behold”, from “ἰδοὺ”, means look at, take notice, observe, see, or gaze at. It is often used as an interjection.<br />*James 3:6 or, Hell<br />*James 4:6 Proverbs 3:34<br />*James 5:4 Greek: Sabaoth (for Hebrew: Tze’va’ot)<br />*James 5:12 TR reads “under judgment” instead of “into hypocrisy”</h5>","path":"/411","Question1":"In this story is there an example that you should imitate? What is it?","Question2":"What steps can you take today to do this?","Question3":"In this story is there an example that you should avoid? What is it?","Question4":"What steps can you take today to do this?","prevPath":"/410","nextPath":"/412"}},"pathContext":{}}

